body {
    margin: 0;
    padding: 0;
    text-align: center;
    color: white;
    font-family: 'ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    background: #141E30;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #243B55, #141E30);
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #243B55, #141E30);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

input {
    font-size: .9rem;
    width: 40%;
    border: none;
    border-radius: .5em;
    padding: .3em 0 .3em 1em;
    outline: none;
    box-shadow: 5px 5px 1em black;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    background: hsl(0, 0%, 0%, .2);
}

.card {
    align-content: center;
    border: 1px solid rgb(87, 84, 84);
    border-radius: 1.5em;
    overflow: hidden;
    margin: 1em;
    width: 36%;
    min-height: 140px;
    background: #000;
    box-shadow: 5px 5px 1em black;
}

.card p {
    text-align: center;
    font-variant: small-caps;
    text-transform: capitalize;
    color: rgb(238, 100, 50);
}

.card img {
    flex: 9;
    width: 100%;
    height: 70%;
    object-fit: cover;
}

hr {
    width: 85%;
    margin-top: 2em;
    opacity: .1;
}

h1 {
    font-family: 'Bangers', cursive, Arial, Helvetica, sans-serif;
    font-variant: small-caps;
    text-shadow: 5px 5px .1em black;
    font-size: 6em;
    letter-spacing: .03em;
}

h3 {
    font-size: .8em;
    font-variant: small-caps;
    text-transform: capitalize;
    text-shadow: 5px 5px .1em black;
}

p {
    font-size: .9em;
    font-style: italic;
    margin: 5px 3px;
}

ol {
    padding: 0;
}


